@import url('https://fonts.googleapis.com/css2?family=Edu+VIC+WA+NT+Beginner&family=Roboto:wght@300&family=Splash&display=swap');

.about{
    padding: 100px;
    height: 600px;
    font-size: 50px;
    margin: auto;
    width: 60%;
    font-weight: bold;
    text-align: center;
    font-family: 'Edu VIC WA NT Beginner', cursive;
}


@media (max-width: 480px) {
    .about{
        padding: 20px;
        height: 680px;
        width:100%;
        font-size: 30px;
    }
}
  
/* Media Query for low resolution  Tablets, Ipads */
@media (min-width: 481px) and (max-width: 767px) {
    .about{
        padding: 30px;
        height: 400px;
        width:80%;
        font-size: 30px;
    }
}

/* Media Query for Tablets Ipads portrait mode */
@media (min-width: 768px) and (max-width: 1024px){
    .about{
        padding: 30px;
        height: 850px;
        width: 80%;
        font-size: 35px;
    }
}