/* contact */
.contact-form{
  height: 500px;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
    align-items: center;
}

.form-control{
    display: flex;
    flex-direction: column;
    text-align: center;
    width:400px;
    height: 30px;
    font-size: 16px;
}
.form-control-message{
    width: 400px;
    height: 200px;
    font-size: 16px;
}

/* contact-button */
.contact-button {
    padding: 1.3em 3em;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    font-weight: 500;
    color: #000;
    background-color: #fff;
    border: none;
    border-radius: 45px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
  }
  
  .contact-button:hover {
    background-color: skyblue;
    box-shadow: 0px 15px 20px skyblue;
    color: #fff;
    transform: translateY(-7px);
  }
  
  .contact-button:active {
    transform: translateY(-1px);
  }
