* {
    margin: 0;
    padding: 0;
}

section {
    width: 100%;
    margin: auto;
    margin-top: 5%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
}

.cards{
    width: 250px;
    padding: 30px;
}

.cards:hover {
    box-shadow: 0 10px 10px 10px gold;
    cursor: pointer;
}

.image_box img {
    width: 150px;
    height: 200px;
}

.details p:nth-child(1){
    font-weight: bold;
}
.details p:nth-child(2){
    color: grey;
    font-size: 1rem;
}
.details p:nth-child(3){
    font-weight: bold;
    color: black;
}
.details button {
    padding: 5px 10px;
    border: none;
    font-weight: bold;
    background-color: skyblue;
    outline: none;
}


/* Media Query for Mobile Devices */
@media (max-width: 480px) {
    section {
        width: 100%;
        height: auto;
        margin: auto;
        margin-top: 5%;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: auto;
    }
}
  
/* Media Query for low resolution  Tablets, Ipads */
@media (min-width: 481px) and (max-width: 767px) {
    section {
        width: 100%;
        margin: auto;
        margin-top: 5%;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: auto;
    }
}
  
/* Media Query for Tablets Ipads portrait mode */
@media (min-width: 768px) and (max-width: 1024px){
    section {
        width: 100%;
        margin: auto;
        margin-top: 5%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: auto;
    }
}

  
