@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&family=Splash&display=swap');

.home-list a{
    font-family: 'Splash', cursive;
    font-size: 70px;
    color: black;
    text-shadow: 0 0 3px skyblue, 0 0 5px gold;
}

.home-list a:hover{
    font-family: 'Splash', cursive;
    font-size: 80px;
    color: black;
    text-shadow: 0 0 3px skyblue, 0 0 5px gold;
}

.home-list{
    padding: 0px;
    color: black;
    display: flex;
    flex-direction: column;
    gap: 6rem;
    margin: 0 auto;
}
.home-box{
    padding: 30px;
    border-radius: 30px;
    padding: 30px auto;
    margin: 30px auto;
    text-align: center;
    width: 40%;
    font-size: 40px;
}


@media (max-width: 480px) {
    .home-list a{
        width: 200px;
        font-size: 60px;
        color: black;
        text-shadow: 0 0 3px skyblue, 0 0 5px gold;
    }
    .home-box{
        height: 800px;
        text-align: left;
        margin: 0 auto;
        padding: 0 auto;
        width: 100%;
    }
}
  
/* Media Query for low resolution  Tablets, Ipads */
@media (min-width: 481px) and (max-width: 767px) {
    .home-list a{
        width: 100%;
        font-size: 50px;
        color: black;
        text-shadow: 0 0 3px skyblue, 0 0 5px gold;
    }
    .home-box{
        height: 600px;
        text-align: center;
        margin: 20px auto;
        padding: 30px auto;
        width: 100%;
    }
}

/* Media Query for Tablets Ipads portrait mode */
@media (min-width: 768px) and (max-width: 1024px){
    .home-list a{
        font-size: 70px;
        color: black;
        text-shadow: 0 0 3px skyblue, 0 0 5px gold;
    }
}