*,*::after, *::before {
    margin: 0;
    box-sizing: border-box;
}
html{
    font-size: 62.5%;
}
body{
    font-size: 1.6rem;
    background-image: url(./back-images/sky-background3.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}
li{
    list-style: none;
}

a{
    text-decoration: none;
    color: darkgray;
}

.nav{
    position: sticky;
    top: 0;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    height: 8vh;
    background: black;
}
.nav__brand{
    color:darkgray;
    text-transform: uppercase;
    font-weight: bold;
}

.nav__menu{
    color:darkgrey;
    display: flex;
    width: 50%;
    align-items: left;
    justify-content: space-around;
    gap: 1rem;
}

.nav__toggler div{
    width: 3rem;
    height: 0.2rem;
    margin: 0.4rem;
    background: darkgray;
    transition: 0.4s ease-in;
}
.nav__toggler{
    cursor: pointer;
    display: none;
}

.nav__item__active{
   font-size: 455px;
   background-color: red;
   background: red;
}

@media (max-width: 480px) {
    body{
        background-image: url(./back-images/sky-background-mobile2.jpg);
        height: 100%;
    }
}

@media screen and (max-width:768px){
    .nav__toggler{
        display: block;
    }
    .nav__menu{
        position: fixed;
        top:7vh;
        right: 0;
        width: 45%;
        height: 50%;
        background: black;
        text-align: center;
        flex-direction: column;
        transform: translateX(100%);
        transition: 0.5s ease-in;
        font-size: 30px;
    }
}

/* Nav Active Class */

.nav__active{
    transform: translateX(0);
}

/* Toggler Icon Animation */
.toggle .line1{
    transform: rotate(-45deg) translate(-4px, 5px);
}
.toggle .line2{
    opacity: 0;
}
.toggle .line3{
    transform: rotate(45deg) translate(-4px, -5px);
}

.carIcon{
    width: 20px;
    height: 20px;
    color: darkgray;
}

.cart span{
    padding: 0 3px;
    font-size: 2rem;
    font-weight: bold;
    border-radius: 5px;
    position: relative;
    top: -5px;
}